var check = require('../../common/utils.js');
export default {
    data() {
        return {
            email: '',
            email1: '',
            emailCode: '',
            codename: ' 获取验证码',
            disabled: false
        };
    },
    onShow() {
        var _this = this;
        uni.request({
            url: this.url + 'delemail/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                var email = res.data.data;
                var email1 = res.data.data;
                _this.email1 = email1;
                var l = email.split('@');
                email = l[0].substr(0, l[0].length - 5) + '****@' + l[1];
                _this.email = email;
            }
        });
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
    methods: {
        getEmailCode: function(e) {
            this.emailCode = e.detail.value;
        },
        sendcode: function() {
            var that = this;
            uni.request({
                //解除绑定邮箱验证码
                url: this.url + 'deleteemail/',
                method: 'POST',
                data: {
                    email: that.email,
                    email_msg: that.emailCode
                },
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success: function(res) {
                    var num = 121;
                    var timer = setInterval(function() {
                        num--;
                        if (num <= 0) {
                            clearInterval(timer);
                            (that.codename = '重新发送'), (that.disabled = false);
                        } else {
                            that.disabled = true;
                            that.codename = num + 's';
                        }
                    }, 1000);
                },
                fail: function(err) {}
            });
        },
        unbind: function() {
            if (this.emailCode == '') {
                uni.showToast({
                    title: '请输入验证码',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            uni.request({
                url: this.url + 'delemail/', //解除绑定接口
                method: 'POST',
                data: {
                    email: this.email1,
                    email_msg: this.emailCode
                },
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '验证码错误',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 200) {
                        uni.showToast({
                            title: '邮箱已解绑',
                            icon: 'none',
                            duration: 2000
                        });
                        uni.switchTab({
                            url: '../../pages/my/my'
                        });
                    }
                }
            });
        }
    }
};
